import React from "react";
import axios from "axios";
import Nav from "./Nav";

function Home(){

    const [intervalRef, setIntervalRef] = React.useState()
    const [currentAngle, setCurrentAngle] = React.useState(0);
    const [currentIndex, setCurrentIndex] = React.useState(0);
    const [playing, setPlaying] = React.useState(false)
    const [canvas, setCanvas] = React.useState([
        {
            "id": "1630595025",
            "title": "Distorted Judgment",
            "link": "https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/29293220014968111021596185839931763830945097708209659344386088227117685800961"
        },
        {
            "id": "1630595052",
            "title": "Beach of Delight",
            "link": "https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/29293220014968111021596185839931763830945097708209659344386088228217197428737"
        },
        {
            "id": "1630595091",
            "title": "Muddled Belief",
            "link": "https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/29293220014968111021596185839931763830945097708209659344386088231515732312065"
        },
        {
            "id": "1630595105",
            "title": "Curiosity of Desire",
            "link": "https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/29293220014968111021596185839931763830945097708209659344386088233714755567617"
        }
    ])

    React.useEffect(()=>{
        shift(0)
        axios.get("https://c17h36.xyz/apis/canvas-extended.json")
            .then(res=>{
                setCanvas(res.data)
                shift(0)
            })
            .catch(console.log)
    },[])

    const playPause = () => {
        if (!playing) {
            let interval = setInterval(() => {
                shift(1)
                rotate(-90)
            }, 3000);
            setIntervalRef(interval)
            setPlaying(true)
        } else {
            clearInterval(intervalRef);
            setPlaying(false)
        }
    };

    const rotate = (delta)=>{
        const cubes = document.querySelectorAll(".cube");

        setCurrentAngle(angle=>{
            Array.from(cubes).forEach(
                (cube) => (cube.style.transform = `rotateY(${angle+delta}deg)`)
            );

            return angle+delta
        })

    }

    const shift = (step)=>{

        const cubeFaces = [
            [
                document.querySelector(".cube-1 .front"),
                document.querySelector(".cube-2 .front"),
                document.querySelector(".cube-3 .front")
            ],
            [
                document.querySelector(".cube-1 .right"),
                document.querySelector(".cube-2 .right"),
                document.querySelector(".cube-3 .right")
            ],
            [
                document.querySelector(".cube-1 .back"),
                document.querySelector(".cube-2 .back"),
                document.querySelector(".cube-3 .back")
            ],
            [
                document.querySelector(".cube-1 .left"),
                document.querySelector(".cube-2 .left"),
                document.querySelector(".cube-3 .left")
            ],
        ]

        const banner = document.querySelector("#banner")

        setCurrentIndex(index=>{
            index = index+step

            // update title color
            banner.setAttribute("style", `background:${getSlide(index)} center;-webkit-background-clip:text;`);

            // update back face
            if (step === -1){
                cubeFaces[mod(index-2,4)][0].style.background = `${getSlide(index-2)} no-repeat 50% 0`
                cubeFaces[mod(index-2,4)][0].style.backgroundSize = "cover"
                cubeFaces[mod(index-2,4)][1].style.background = `${getSlide(index-2)} no-repeat 50% -7vw`
                cubeFaces[mod(index-2,4)][1].style.backgroundSize = "cover"
                cubeFaces[mod(index-2,4)][2].style.background = `${getSlide(index-2)} no-repeat 50% -14vw`
                cubeFaces[mod(index-2,4)][2].style.backgroundSize = "cover"
            } else {
                cubeFaces[mod(index + 2, 4)][0].style.background = `${getSlide(index + 2)} no-repeat 50% 0`
                cubeFaces[mod(index + 2, 4)][0].style.backgroundSize = "cover"
                cubeFaces[mod(index + 2, 4)][1].style.background = `${getSlide(index + 2)} no-repeat 50% -7vw`
                cubeFaces[mod(index + 2, 4)][1].style.backgroundSize = "cover"
                cubeFaces[mod(index + 2, 4)][2].style.background = `${getSlide(index + 2)} no-repeat 50% -14vw`
                cubeFaces[mod(index + 2, 4)][2].style.backgroundSize = "cover"
            }

            // update left face
            cubeFaces[mod(index-1,4)][0].style.background = `${getSlide(index-1)} no-repeat 50% 0`
            cubeFaces[mod(index-1,4)][0].style.backgroundSize = "cover"
            cubeFaces[mod(index-1,4)][1].style.background = `${getSlide(index-1)} no-repeat 50% -7vw`
            cubeFaces[mod(index-1,4)][1].style.backgroundSize = "cover"
            cubeFaces[mod(index-1,4)][2].style.background = `${getSlide(index-1)} no-repeat 50% -14vw`
            cubeFaces[mod(index-1,4)][2].style.backgroundSize = "cover"

            // update front face
            cubeFaces[mod(index,4)][0].style.background = `${getSlide(index)} no-repeat 50% 0`
            cubeFaces[mod(index,4)][0].style.backgroundSize = "cover"
            cubeFaces[mod(index,4)][1].style.background = `${getSlide(index)} no-repeat 50% -7vw`
            cubeFaces[mod(index,4)][1].style.backgroundSize = "cover"
            cubeFaces[mod(index,4)][2].style.background = `${getSlide(index)} no-repeat 50% -14vw`
            cubeFaces[mod(index,4)][2].style.backgroundSize = "cover"

            // update right face
            cubeFaces[mod(index+1,4)][0].style.background = `${getSlide(index+1)} no-repeat 50% 0`
            cubeFaces[mod(index+1,4)][0].style.backgroundSize = "cover"
            cubeFaces[mod(index+1,4)][1].style.background = `${getSlide(index+1)} no-repeat 50% -7vw`
            cubeFaces[mod(index+1,4)][1].style.backgroundSize = "cover"
            cubeFaces[mod(index+1,4)][2].style.background = `${getSlide(index+1)} no-repeat 50% -14vw`
            cubeFaces[mod(index+1,4)][2].style.backgroundSize = "cover"

            // update link title
            cubeFaces[mod(index,4)][0].parentElement.title = getTitle(index)
            cubeFaces[mod(index,4)][1].parentElement.title = getTitle(index)
            cubeFaces[mod(index,4)][2].parentElement.title = getTitle(index)

            // update link href
            cubeFaces[mod(index,4)][0].parentElement.href = getLink(index)
            cubeFaces[mod(index,4)][1].parentElement.href = getLink(index)
            cubeFaces[mod(index,4)][2].parentElement.href = getLink(index)

            return(index);
        })

    }

    const mod = (n, m)=>{
        return ((n % m) + m) % m;
    }

    const getSlide = (index)=>{
        let n = canvas.length
        const baseUrl = 'https://firebasestorage.googleapis.com/v0/b/chjvegltyq.appspot.com/o/canvas-of-consciousness%2F'
        const extension = '.jpg?alt=media'
        return(`url('${baseUrl+canvas[mod(index,n)].id+extension}')`)
    }

    const getTitle = (index)=>canvas[mod(index,canvas.length)].title
    const getLink = (index)=>canvas[mod(index,canvas.length)].link

    return(
        <React.Fragment>
            <div className="body">
                <center style={{color:"white",paddingTop:'5px'}}>
                    <h1>Canvas of <span id="banner">Consciousness</span></h1>
                </center>
                <div className="viewport">
                    <div className="control left-arrow"
                         onClick={()=> {
                             shift(-1)
                             rotate(90)
                             if(playing) playPause()
                         }}
                         onMouseOver={()=>rotate(+25)}
                         onMouseOut={()=>rotate(-25)}
                    >
                        <i className="fas fa-arrow-left"/>
                    </div>

                    <div className="cube cube-1">
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face front"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face back"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face top"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face bottom"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face left"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face right"/>
                        </a>
                    </div>

                    <div className="cube cube-2">
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face front"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face back"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face top"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face bottom"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face left"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face right"/>
                        </a>
                    </div>

                    <div className="cube cube-3">
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face front"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face back"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face top"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face bottom"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face left"/>
                        </a>
                        <a href="/" title="" target="_blank" rel="noreferrer">
                            <div className="face right"/>
                        </a>
                    </div>

                    <div className="control right-arrow"
                         onClick={()=> {
                             shift(+1)
                             rotate(-90)
                             if(playing) playPause()
                         }}
                         onMouseOver={()=>rotate(-25)}
                         onMouseOut={()=>rotate(+25)}
                    >
                        <i className="fas fa-arrow-right"/>
                    </div>

                    <div className="control play-pause" onClick={playPause}>
                        {playing? <i className="fas fa-pause"/> : <i className="fas fa-play"/>}
                    </div>


                </div>
                <Nav/>
            </div>
        </React.Fragment>
    )
}
export default Home